import { useState } from "react";
import axios from "axios";

function Input() {
	const [inputData, setInputData] = useState({});
	const [stringData, setStringData] = useState([]);

	const handleChange = (event) => {
		const updatedField = { [event.target.name]: event.target.value };

		const editedMake = Object.assign(inputData, updatedField);

		setInputData(editedMake);
	};

	const handleSubmit = async (event) => {
		event.preventDefault();

		await axios({
			url: "/test",
			method: "POST",
			data: inputData,
		})
			.then((res) => setStringData(res.data.return_string))

			.catch((err) => console.log(err));
	};

	return (
		<div className="py-3 font-poppins">
			<form>
				<h1 className="text-3xl py-3">
					Send a string to <span className="opacity-30">/test!</span>
				</h1>
				<div className="flex justify-center">
					<input
						name="string_to_cut"
						onChange={handleChange}
						placeholder="e.g. iamyourlyftdriver"
						className="border text-center mx-3 py-3 px-10 w-10/12 lg:w-1/4"
						type="text"
					/>
				</div>
			</form>
			<button
				className="text-2xl bg-lyftLogoColor px-3 py-2 my-3 text-white rounded-lg hover:bg-lyftDark transition ease-in-out"
				onClick={handleSubmit}
			>
				Send!
			</button>
			{stringData.length > 0 ? (
				<h1 className="text-2xl">
					Here is your output: <span className="text-lyftLogoColor">{stringData}</span> üåü
				</h1>
			) : (
				<h1 className="text-2xl pt-3">
					Awaiting output... <em>Beep Beep!</em> üöó
				</h1>
			)}
		</div>
	);
}

export default Input;
